import React from "react";
import {Link} from "react-router-dom"
import teir from "../Navbar/teir.png";
import "./Navbar.css";


export const Navbar=()=>{
    const [isMobile,setisMobile]=React.useState(false)
    return(
        <div className="navbar">
            <ul className={isMobile ? "nav-link-mobile" : "nav-links"} onClick={()=> setisMobile(false)}>
                <Link to="/" className="home">
                    <li>HOME</li>
                </Link>
                <Link to="/services" className="services">
                    <li>SERVICES</li>
                </Link>
                <Link to="/faqs" className="faqs">
                    <li>FAQ</li>
                </Link>
                <Link to="/contact" className="contact">
                    <li>CONTACT US</li>
                </Link>
            </ul>
            <button className="nav-menu" onClick={() => setisMobile(! isMobile)}>
                {isMobile ? <i className="fa-solid fa-xmark"></i> : <i className="fa-solid fa-bars"></i>}
            </button>
            <img src={teir} alt=""  className="nav-pic"/>
        </div>
    )
}
