import React from "react";
import {Navbar} from "./component/Navbar/Navbar";
import {HashRouter, Routes, Route } from "react-router-dom";
import {Header}  from "./component/Header";
import {Home} from "./component/Home";
import {Services} from "./component/Services";
import {Faqs} from "./component/Faqs";
import {Contact} from "./component/Contact";
import { Myaccount } from "./component/Myaccount";
import { ITstartData } from "./component/Home-startData";
import { ServicesData } from "./component/Service-Data";
import { FaqsData } from "./component/Faq-Data";
import { ContactData } from "./component/ContactData";
import { MyacData } from "./component/MyacData";






function App() {

  const DataElement=ITstartData.map(HoData =>{
    return(
      <Home{...HoData} key={HoData.id} />
      )
    });
  const AccountElement=MyacData.map(acData =>{
    return(
      <Myaccount  {...acData} key={acData.id}/>
    )
  })


  const contactElement=ContactData.map(conData =>{
    return(
      <Contact {...conData} key={conData.id}/>
    )
  })

  const faqElement=FaqsData.map(faq =>{
    return(
      <Faqs {...faq} key={faq.id}/> 
    )
  });


  const servicesElement=ServicesData.map(serve =>{
    return(
      <Services {...serve} id={serve.id}/>
    )
  });

    
  return (
    <div>
    <HashRouter>
    <Header/>
    <Navbar />
    <Routes>
      <Route path="/"  element={[DataElement]}/> 
      <Route path="/account" element={[AccountElement]}/>
      <Route path="/services" element={[servicesElement]}/>
      <Route path="/faqs" element={[faqElement]}/>
      <Route path="/contact" element={[contactElement]}/>
    </Routes>
    </HashRouter>
    </div>
  )
}

export default App;
